.sortviz {
  max-width: 720px;
  margin: auto;
  padding: 0 10px;

  &-accent {
    color: #533ca2;
  }

  &-main {
    &-caption {
      font-size: 28px;
    }
  }

  &-block {
    padding: 12px 0px;
  }

  &-button {
    min-width: 140px;
    height: 48px;
    font-size: 24px;
    border: 1px solid #533ca2;
    margin: 0 6px;
    // text-transform: uppercase;
    background: none;
    color: #533ca2;
    cursor: pointer;
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;

    span {
      font-size: 12px;
    }

    &:disabled {
      border-color: #bbb;
      color: #bbb;
    }

    &:hover:not(:disabled) {
      box-shadow: 0px 1px 2px #533ca244
    }
  }

  &-buttons {
    display: flex;
    justify-content: center;
  }

  &-canvas {
    width: 100%;
    height: 200px;

    &-wrapper {
      width: 100%;
      margin-bottom: 20px;
      border-bottom: 4px solid #533ca2;
    }
  }

  &-link {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-left: 6px;
    overflow: hidden;
    line-height: 0;
    &:hover {
      opacity: .5
    }
  }
}