.slidedown {
  &-button {
    border: 1px solid #533ca2;
    background: none;
    color: #533ca2;
    font-family: inherit;
    border-radius: 8px;
    padding: 2px  8px;
    cursor: pointer;
    transition: background .2s, color .2s;

    &:hover {
      background: #533ca2;
      color: #eee;
    }
  }
}

