}


.sortviz {
  max-width: 720px;
  margin: auto;
  padding: 0 10px; }
  .sortviz-accent {
    color: #533ca2; }
  .sortviz-main-caption {
    font-size: 28px; }
  .sortviz-block {
    padding: 12px 0px; }
  .sortviz-button {
    min-width: 140px;
    height: 48px;
    font-size: 24px;
    border: 1px solid #533ca2;
    margin: 0 6px;
    background: none;
    color: #533ca2;
    cursor: pointer;
    font-family: 'Source Code Pro', monospace;
    font-weight: 400; }
    .sortviz-button span {
      font-size: 12px; }
    .sortviz-button:disabled {
      border-color: #bbb;
      color: #bbb; }
    .sortviz-button:hover:not(:disabled) {
      box-shadow: 0px 1px 2px #533ca244; }
  .sortviz-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center; }
  .sortviz-canvas {
    width: 100%;
    height: 200px; }
    .sortviz-canvas-wrapper {
      width: 100%;
      margin-bottom: 20px;
      border-bottom: 4px solid #533ca2; }
  .sortviz-link {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-left: 6px;
    overflow: hidden;
    line-height: 0; }
    .sortviz-link:hover {
      opacity: .5; }

.slidedown-button {
  border: 1px solid #533ca2;
  background: none;
  color: #533ca2;
  font-family: inherit;
  border-radius: 8px;
  padding: 2px  8px;
  cursor: pointer;
  transition: background .2s, color .2s; }
  .slidedown-button:hover {
    background: #533ca2;
    color: #eee; }

